.skill {
  border: 1px solid white;
  width: 340px;
  background-color: #252527;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.iconBlock {
  color: #4e93e6;
  height: 80px;
  width: 80px;
  margin: 0 auto 30px;
  background: rgba(255,255,255,0.1);
  display: block;
  line-height: 80px;
  font-size: 50px;
  /*box-shadow: -6px 10px 40px rgb(0 0 0 / 6%);*/
  text-align: center;
  transform: rotate(45deg);
  transform-origin: center;
  font-weight: 400;

}

.icon {
  transform: rotate(-45deg);
}

.title {
  color: white;
  text-transform: uppercase;
  margin: 0 0 15px;
  padding: 0;
  font-size: 16px;
  letter-spacing: 1px;
  font-family: "Josefin Sans", sans-serif;
  font-weight: 700;
  line-height: 1.2;
}

.description {
  font-size: 14px;
  line-height: 1.8;
  font-family: 'Poppins', sans-serif;
  color: white;
}